<template>
  <va-card title="Proforma Invoices">
    <div class="row align--center mb-1">
      <div class="flex xs12 sm6">
        <va-input
          class="ma-0"
          :value="term"
          :placeholder="$t('tables.searchByName')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
      </div>
      <va-button
          slot="actions"
          color="success"
          class="ma-0"
          @click="proformaGo"
        >
         {{ $t('dashboard.proforma') }}
        </va-button>
    </div>


    <va-data-table
      :fields="mode ? detailedFields : fields"
      :data="filteredData"
      :loading="loading"
      hoverable
    >
      <template v-slot:inv_number="props">
        {{ props.rowData.inv_number }}
      </template>
      <template v-slot:posting_date="props">
        {{ props.rowData.inv_date | formatLongDate }}
      </template>
      <template style="text-align: right;" v-slot:amount="props">
        <div style="text-align: left;">{{ props.rowData.inv_amount | Currency }}</div>
      </template>
      <template  v-slot:inv_numbe="props" >
          <va-button icon="fa fa-eye" color="primary" @click="goToProforma(props.rowData.inv_number)"/>
      </template>
    </va-data-table>
  </va-card>
</template>

<script>
import axios from 'axios'
import debounce from 'lodash/debounce'

export default {
  data () {
    return {
      trans: {},
      loading: false,
      term: null,
      inv_number: '',
      invoice: {},
      mode: 0,
      userData: JSON.parse(localStorage.getItem('LoggedUser')),
      token: JSON.parse(localStorage.getItem('Token')),
    }
  },
  created () {
    this.getProformainvoices()
    // this.createProformainvoices()
  },
  computed: {
    fields () {
      return [{
        name: '__slot:inv_number',
        title: this.$t('tables.headings.inv_number'),
        width: '50%',
      }, {
        name: '__slot:posting_date',
        title: this.$t('tables.headings.date'),
        width: '30%',
      }, {
        name: '__slot:amount',
        title: this.$t('tables.headings.amount'),
        width: '20%',
        dataClass: 'text-right',
      },
      {
        name: '__slot:inv_numbe',
        title: "Action",
        width: '30px',
        dataClass: 'text-center',
      }]
    },
    filteredData () {
      if (!this.term || this.term.length < 1) {
        return this.trans
      }
      console.log(this.term);
      return this.trans.filter(item => {
        return item.description.toLowerCase().startsWith(this.term.toLowerCase()) || item.amount.toLowerCase().startsWith(this.term.toLowerCase())
      })
    },
  },
  methods: {
    getProformainvoices () {
      const self = this

      let config = {
        headers: {
          Authorization: this.userData.user_id+' '+this.token
        }
      }

      axios.post(`${self.$apiUrl}getproformainvoices`, this.userData.user_id, config)
        .then(function (res) {
          res = res.data
          self.trans = res.slice()
          //self.inv_number = res.inv_number
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    // createProformainvoices () {
    //   const self = this

    //   let config = {
    //     headers: {
    //       Authorization: this.userData.user_id+' '+this.token
    //     }
    //   }

    //   axios.post(`${self.$apiUrl}generateproformainvoices`, this.userData.user_id, config)
    //     .then(function (res) {
    //       res = res.data
    //       self.trans = res.slice()
    //       //self.inv_number = res.inv_number
    //     })
    //     .catch(function (error) {
    //       console.log(error)
    //     })
    // },
    

    goToProforma(inv_number){
      this.$router.push({name: "generate-proforma", params: { inv_num:inv_number, type:'PRO'}});    
    },

    proformaGo(){
      const self = this

      let config = {
        headers: {
          Authorization: this.userData.user_id+' '+this.token
        }
      }

      axios.post(`${self.$apiUrl}generateproformainvoices`, this.userData.user_id, config)
        .then(function (res) {
          res = res.data
        
          self.$router.push({name: "generate-proforma", params: { inv_num:res }});
        })
        .catch(function (error) {
          console.log(error)
        })       

    },

    search: debounce(function (term) {
      console.log(term)
      this.term = term
    }, 400),
  },
}
</script>

<style lang="scss">
.mr-0 {
  margin-right: 0;
}
</style>
